import React, {Component} from 'react';
import './wrapper.css';
import '../../static/iconfont/iconfont.css';
import {Link} from 'react-router-dom';

const active_bg = 'grey';
const normal_bg = '#282c34';

export default (title) => WrappedComponent => class Wrapper extends Component{
    constructor(props){
        super(props);
        this.state = {
            menuArr:[
                {
                    text:'首页',
                    link:'/home'
                }, {
                    text:'博客文章',
                    link:'/blog'
                }, {
                    text:'留言社区',
                    link:'/comment'
                }, {
                    text:'软件分享',
                    link:'/app'
                }, {
                    text:'其他内容',
                    link:'/others'
                }
            ],
            userArr:[
                {
                    text:'个人中心'
                }, {
                    text:'账号设置'
                }, {
                    text:'退出登录'
                }
            ],
            menuDisplay:'none',
            userDisplay:'none',
            menuBg:normal_bg,
            userBg:normal_bg
        }
    }

    onClink=(e)=>{
        let display = null;
        let Bg = null;
        if(e.target.value==='menu'){
            if(this.state.menuDisplay==='none'){
                display = 'block';
                Bg = active_bg
            }else{
                display = 'none';
                Bg = normal_bg;
            }
           this.setState({
               menuDisplay:display,
               menuBg:Bg,
               userDisplay:'none',
               userBg:normal_bg
           })
       }
       else if(e.target.value==='user'){
           if(this.state.userDisplay==='none'){
                display = 'block';
                Bg = active_bg
            }else{
                display = 'none';
                Bg = normal_bg;
            }
           this.setState({
               userDisplay:display,
               userBg:Bg,
               menuDisplay:'none',
               menuBg:normal_bg
           })
       }
    };
    loseBlur=(e)=>{
       // if(e.target.value==='menu'){
       //     this.setState({
       //         menuDisplay:'none',
       //         menuBg:'#282c34'
       //     })
       // }
       // else if(e.target.value==='user'){
       //     this.setState({
       //         userDisplay:'none',
       //         userBg:'#282c34'
       //     })
       // }
    };


    render() {
        return (
            <div id='wrapper'>
                <div id='wrapper-background'>
                </div>
                <div id='div-top'>
                    <div id='div-top-relative'>
                        <button id='btn-menu' value='menu' className={"wrapper-btn iconfont icon-weibiaoti12"}  style={{background:this.state.menuBg}} onClick={this.onClink} onBlur={this.loseBlur}/>
                        <span id='title'>{title}</span>
                        <button id='btn-user' value='user' className={"wrapper-btn iconfont icon-yonghu"} style={{background:this.state.userBg}} onClick={this.onClink} onBlur={this.loseBlur}/>
                    </div>
                </div>
                <div id='div-menu' style={{display:this.state.menuDisplay}}>
                        <ul className='wrapper-ul'>
                            {
                                this.state.menuArr.map((item,key)=>(
                                    <li className='wrapper-li' key={key}><Link to={item.link}>{item.text}</Link></li>
                                ))
                            }
                        </ul>
                </div>
                <div id='div-user' style={{display:this.state.userDisplay}}>
                        <ul className='wrapper-ul'>
                            {
                                this.state.userArr.map((item,key)=>(
                                    <li className='wrapper-li' key={key}>{item.text}</li>
                                ))
                            }
                        </ul>
                </div>
                <div id='div-wrapped'>
                    <WrappedComponent/>
                </div>
            </div>
        );
    }
}
